<template>
<div class="content-wrapper">
    <div class="container">
        <div class="login-box">
            <div class="login-box-body">
                <p v-if="!isRegister" class="login-box-msg">Đăng ký thành viên tizi</p>
                <form v-if="!isRegister" v-on:submit="register" action="javascript:void(0)">
                    <div class="form-group has-feedback">
                        <input v-on:keyup="checkUsername" v-model="user.username" name="username" v-validate="'required|min:5|regex:^[a-zA-Z0-9-_]*$'" type="text" class="form-control" placeholder="Tên đăng nhập..." />
                        <span class="glyphicon glyphicon-user form-control-feedback"></span>
                        <span class="error">{{ errors.first("username") }}</span>
                        <span v-if="isUsername" class="error">Tài khoản đã được sử dụng !</span>
                    </div>
                    <div class="form-group has-feedback">
                        <input v-model="user.fullname" name="fullname" v-validate="'required'" type="text" class="form-control" placeholder="Họ tên">
                        <span class="glyphicon glyphicon-user form-control-feedback"></span>
                        <span class="error">{{ errors.first("fullname") }}</span>
                    </div>
                    <div class="form-group has-feedback">
                        <input v-on:keyup="checkEmail" v-model="user.email" name="email" v-validate="'required|email'" type="email" class="form-control" placeholder="Email">
                        <span class="glyphicon glyphicon-envelope form-control-feedback"></span>
                        <span class="error">{{ errors.first("email") }}</span>
                        <span v-if="isEmail" class="error">Email đã được sử dụng !</span>
                    </div>
                    <div class="form-group has-feedback">
                        <input v-on:keyup="checkPhone" v-model="user.phone" name="phone" v-validate="'required|min:10|max:10'" type="text" class="form-control" placeholder="SDT">
                        <span class="glyphicon glyphicon-phone form-control-feedback"></span>
                        <span class="error">{{ errors.first("phone") }}</span>
                        <span v-if="isPhone" class="error">SDT đã được sử dụng !</span>
                    </div>
                    <div class="form-group has-feedback">
                        <input v-model="user.password" name="password" v-validate="'required|min:6'" type="password" class="form-control" placeholder="Mật khẩu" id="password">
                        <span class="glyphicon glyphicon-lock form-control-feedback"></span>
                        <span class="error">{{ errors.first("password") }}</span>
                    </div>
                    <div class="form-group has-feedback">
                        <input v-model="user.rPassword" name="rPassword" v-validate="'required|min:6|rPassword'" type="password" class="form-control" placeholder="Nhập lại mật khẩu" id="rPassword">
                        <span class="glyphicon glyphicon-log-in form-control-feedback"></span>
                        <span class="error">{{ errors.first("rPassword") }}</span>
                    </div>
                    <div class="row">
                        <div class="col-xs-8"></div>
                        <div class="col-xs-4">
                            <button v-if="!isUsername && !isEmail && !isPhone" type="submit" class="btn btn-primary btn-block btn-flat">Đăng ký</button>
                        </div>
                    </div>
                </form>
                <p v-if="isRegister" class="login-box-msg">Xác thực tài khoản !</p>
                <form v-if="isRegister" action="javascript:void(0)">
                    <div class="form-group has-feedback">
                        <input v-model="code" name="code" v-validate="'required|min:4'" type="text" class="form-control" placeholder="Kiểm tra email > nhập code...">
                        <span class="glyphicon glyphicon-log-in form-control-feedback"></span>
                        <span class="error">{{ errors.first("code") }}</span>
                    </div>
                    <div class="row">
                        <div class="col-xs-8">
                            <a v-on:click="confirmMail" href="#">Gửi lại code</a>
                        </div>
                        <div class="col-xs-4">
                            <button v-on:click="confirm" type="submit" class="btn btn-primary btn-block btn-flat">Xác nhận</button>
                        </div>
                    </div>
                </form>
            </div>
        </div>
    </div>
</div>
</template>

<script>
import { Validator } from "vee-validate";
import axios from 'axios';

export default {
    data(){
        return {
            api: this.$store.state.domainApi,
            isRegister: this.$store.state.user.status == 0 ? true : false,
            isUsername: false,
            isEmail: false,
            isPhone: false,
            code:'',
            user:{
                username:'',
                fullname:'',
                email:'',
                phone: this.$route.query.phone ? this.$route.query.phone : '',
                password:'',
                rPassword:''
            },
            dictionary: {
                vi: {
                    custom: {
                        "username":{
                            required: "Vui lòng nhập tài khoản !",
                            regex: "Tên tài khoản bào gồm chữ và số không có đấu cách"
                        },
                        "fullname":{
                            required: "Vui lòng nhập họ tên !"
                        },
                        "email":{
                            required: "Vui lòng nhập email !",
                            email: "Vui lòng nhập đúng định dạng email !",
                        },
                        "phone":{
                            required: "Vui lòng nhập SDT !",
                            min: "SDT phải là 10 số",
                            max: "SDT phải là 10 số",
                        },
                        "password":{
                            required: "Vui lòng nhập mật khẩu !",
                            min: "Mật khẩu tối thiểu 6 ký tự !",
                        },
                        "rPassword":{
                            required: "Vui lòng nhập mật khẩu !",
                            min: "Mật khẩu tối thiểu 6 ký tự !",
                        },
                        "code": {
                            required: "Vui lòng nhập code!",
                            min: "Code có 4 ký tự !"
                        }
                    }
                }
            }
        }
    },
    methods: {
        register(){
            this.$validator.validate().then(async valid => {
                if(valid){
                    this.$swal.fire({
                        title: 'Đang đăng ký',
                        text: "Vui lòng chờ !",
                        allowOutsideClick: false
                    });
                    this.$swal.showLoading();
                    const user = await axios.post(`${this.api}/auth/create`, this.user);
                    if(user.data.success===1){
                        this.flash(user.data.message, 'success', {
                            timeout: 3000
                        });
                        await this.login();
                        this.isRegister = true;
                        this.$swal.close();

                        if(localStorage.id_product){
                            var id_product = JSON.parse(localStorage.id_product);
                            await axios.patch(`${this.api}/auth/update-user-product`,{
                                id_user: user.data.id_user,
                                id_product: id_product 
                            });
                            localStorage.removeItem('id_product');
                        }
                    }else this.$swal(user.data.message,"", "error");
                }
            })
            .catch(error=>{
                console.log(error);
            });
        },
        async login(){
            const user = await axios.post(`${this.api}/auth/login`, this.user);
            if(user.data.success===1){
                localStorage.token = user.data.token;
                this.$store.state.token = user.data.token;
                this.$store.state.user = user.data.user;
                this.flash(user.data.message, 'success', {
                    timeout: 3000
                });
            }else this.$swal(user.data.message,"", "error");
        },
        async checkUsername(e){
            var data = {username: e.target.value};
            if(e.target.value){
                var check = await this.check(data)
                this.isUsername = check.result;
            }
        },
        async checkEmail(e){
            var data = {email: e.target.value};
            if(e.target.value){
                var check = await this.check(data)
                this.isEmail = check.result;
            }
        },
        async checkPhone(e){
            var data = {phone: e.target.value};
            if(e.target.value){
                var check = await this.check(data)
                this.isPhone = check.result;
            }
        },
        async check(data){
            var check = await axios.get(`${this.api}/auth/check`,{
                params: data
            });
            return check.data;
        },
        async confirm(){
            const user = await axios.put(`${this.api}/auth/active`, {
                username: this.$store.state.user.username,
                code: this.code
            });
            if(user.data.success===1){
                this.$store.state.user.status = 1;
                this.flash(user.data.message, 'success', {
                    timeout: 3000
                });
                this.$router.push({name:'home'});
            }else this.$swal(user.data.message,"", "error");
        },
        async confirmMail(){
            const mail = await axios.put(`${this.api}/auth/confirmMail`, {
                username: this.$store.state.user.username,
                email: this.$store.state.user.email
            });
            if(mail.data.success===1){
                this.flash(mail.data.message, 'success', {
                    timeout: 3000
                });
            }else this.$swal(mail.data.message,"", "error");
        }
    },
    created(){
        Validator.localize(this.dictionary);
        const validator = new Validator({ first_name: 'alpha' });
        validator.localize('vi');

        Validator.extend('rPassword', {
            getMessage: () => 'Mật khẩu không khớp !',
            validate(){
                let password = document.getElementById("password");
                let rPassword = document.getElementById("rPassword");
                if(password.value==rPassword.value) return true;
                return false;
            }
        });
    }
}
</script>

<style>
.error{
    display: block;
    color: red;
    margin-left: 1px;
}
.content-wrapper{
    min-height: 682px;
}
@media(max-width: 768px){
    .content-wrapper{
        min-height: 100%;
    }
}
</style>